/* CUSTOM */
/* Start::body */
body {
  font-size: 0.875rem;
  font-family: var(--default-font-family);
  font-weight: var(--default-font-weight);
  background-color: var(--default-body-bg-color);
  color: var(--default-text-color);
  line-height: 1.5;
  text-align: start;
  overflow-x: clip;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-right: 0 !important;
  overflow: auto !important;
}

/* End::body */
*::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
  transition: all ease 0.05s;
}

*:hover::-webkit-scrollbar-thumb {
  background: rgb(var(--light-rgb));
}

a,
button {
  outline: 0 !important;
}

/* Start::basic */
.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

@media (min-width: 992px) {
  .app-content {
    min-height: calc(100vh - 7.5rem);
    margin-block-end: 0;
    margin-block-start: 4rem;
    margin-inline-start: 15rem;
    transition: all 0.05s ease;
  }
}
.main-content {
  padding: 0 0.8rem;
}

/* End::basic */
/* Start::App Content */
.bd-example > .dropdown-menu {
  position: static;
  display: block;
}

/* Start::width */
.min-w-fit-content {
  min-width: fit-content;
}

/* End::width */
/* Start::Scrollspy */
.scrollspy-example {
  height: 12.5rem;
  margin-block-start: 0.5rem;
  overflow: auto;
}

.scrollspy-example-2 {
  height: 21.875;
  overflow: auto;
}

.scrollspy-example-3 {
  height: 13.75rem;
  overflow: auto;
}

.simple-list-example-scrollspy .active {
  background-color: var(--primary-color);
  color: #fff;
}

.scrollspy-example-4 {
  height: 12.5rem;
  margin-block-start: 0.5rem;
  overflow: auto;
